* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  
  .App {
    text-align: center;
  }
.mySwiper {
  width: 100%;
  height: 95vh;
  display: flex;
  
}
  main {
    
    width: 100%;
    height: 100%; /* Change to vh for viewport height */
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden; /* Ensures the image does not overflow the main container */
    position: relative;
  }
  .slide1 {
    background: linear-gradient(to bottom, #75757525 80%, #20202000), url("/src/Images/image2.jpg") ;
    background-size: cover;
    width: 100%;
    height: 95vh;
    background-attachment: fixed;

  }
  .slide2 {
    background: linear-gradient(to bottom, #75757525 80%, #20202000), url("/src/Images/image4.jpg") ;
    background-size: cover;
    width: 100%;
    height: 95vh;
    background-attachment: fixed;

  }
  .slide3 {
    background: linear-gradient(to bottom, #75757525 80%, #20202000), url("/src/Images/image5.jpg") ;
    background-size: cover;
    width: 100%;
    height: 95vh;
    background-attachment: fixed;

  }
  .mySwiper .swiper-button-next,
.swiper-button-prev {
    background-color: #1a1a1acc;
    padding: 30px;
    color: #f9f9f9 !important;
}
  main .placement {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(145, 145, 145, 0.35);
    backdrop-filter: blur(5px);
    height: 35%;
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    letter-spacing: 2px;
}

main .slogan h1 {
    text-transform: capitalize;
    color: #181818;
    text-align: center;
    font-size: 2.5rem;
}
  
  main .slogan:hover {
    transform: scale(1.05);
  }
  

  .newsletter
{
    width: auto;
    height: 55dvh;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 18rem;
    background: url("/src/Images/image3.jpg") center;
    background-attachment: fixed;
}
.newsletter .container_letter
{
    display: flex;
    gap: 2rem;
    flex-direction: column;
}

 
.newsletter .container_letter h1{
    color: #fcfcfe;
    font-size: 3rem;
}

.newsletter .container_letter form 
{

    gap: 15px;

}
.newsletter .container_letter form input

{
    color: #f3f5f4;
    width: 450px;
    height: 50px;
    padding: 15px;
    font-size: 1.3rem;
    outline: none;
    border: none;
background: rgba(223, 223, 223, 0.123);
box-shadow: 0 4px 30px rgba(68, 37, 20, 0.164);
backdrop-filter: blur(3.7px);
position: relative;
}
.newsletter .container_letter form::after {
    content: "";
    position: absolute;
    bottom: 0px;
    left: 0;
    width:450px;
    height: 3px;
    scale: 0;
    transition: 0.5s ease-in-out;
    transform-origin: left;
    background-color: #0099ff;
}
.newsletter .container_letter  form:hover::after 
{

    scale: 1;
    transition: 0.5s ease-in-out;

}

.newsletter .container_letter form button
{
    width: 200px;
    height: 50px;
    font-size: 20px;
    color: #fff;
    border: none;
    cursor: pointer;
    background-image: linear-gradient(to right, #2a067e 0%, #1b345a  51%, #FF512F  100%);
        background-size: 200% auto;
        transition: 0.5s;
              
}

.newsletter .container_letter form button:hover
{
    background-position: right center; /* change the direction of the change here */
    color: #fff;
    box-shadow: 0 0 20px #444;
}
@media only screen and
(max-device-width: 580px) and 
(min-device-width:320px)
{ 
  main {
   
    width: 100%;
    height: 60vh; /* Change to vh for viewport height */
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden; /* Ensures the image does not overflow the main container */
    position: relative;
  }
main .placement {
  width: 45%;
  height: 30%;
}
main .slogan h1 {
  text-transform: capitalize;
  color: #181818;
  text-align: center;
  font-size: 1.5rem;
}
  .newsletter
{
  width: auto;
  height: auto;
  display: flex;
  background-size: cover;
  align-items: center;
  justify-content: center;
  margin-top: 5rem;

}
